import {
	friendlyDate
} from '@/common/util.js';
import sunTab from '@/components/sun-tab/sun-tab.vue';
import MescrollUni from "@/components/mescroll-uni/mescroll-uni.vue";
import sibList from '@/components/sib-list/sib-list.vue';
var _self,pageNum,newsLength;
export default {
	data() {
		return {
			isread:0,//是否阅读
			tabIndex: 0,
			scrollViewHeight:0,//newsList的整体高度
			swiperIndex:0,//滑动标签
			test:[],
			tabtopList:[
				{
					id:"1",
					title:"系统公告"
				},
				{
					id:"2",
					"title":"通知公告"
				}
				
			],
			newsList:[],
			downOption: { 
				use: true, // 是否启用下拉刷新; 默认true
				auto: true, // 是否在初始化完毕之后自动执行下拉刷新的回调; 默认true
			},
			upOption: {
				use: true, // 是否启用上拉加载; 默认true
				auto: true, // 是否在初始化完毕之后自动执行上拉加载的回调; 默认true
				page: {
				num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
				size: 10 // 每页数据的数量,默认10
				},
				noMoreSize: 5, // 配置列表的总数量要大于等于5条才显示'-- END --'的提示
				empty: {
					tip: '暂无相关数据'
				},
				textNoMore:'没有更多消息了'
				},
		}
	},
	onLoad:function(){
		_self=this;
		this.getNewsList();
	},
	
	methods: {
		getNewsList:function(){
			uni.request({
				header:{
					'sessionId': 'fa2e5c5698b0480eb68ec05fa9a35a3d' //自定义请求头信息
				},
				url: 'http://192.168.1.200:40881/bulletin/queryBulletinTitlesByPage',
				success:function(res){
					const data=res.data.data[0];
					console.log(data.pageSize);
					
					//判断是否有
					for (var i=0;i<data.pageSize;i++){ 
							if(data.results[i].isread!=0){
								_self.isread++;
							}
					}
					newsLength=data.pageSize;
					if(_self.isread==0){
						return
					}else{
						_self.tabtopList[0].title='通知公告('+_self.isread+')';
					}
					console.log(_self.isread+'dfdf');
				},
				fail:function(){
					
				}
			})
		},
		tabTopClick:function(e){
			_self.tabIndex=_self.swiperIndex=e;
			//console.log(e)
		},
		animationfinish:function({ detail: { current } }){
			console.log(current);
			_self.tabIndex=_self.swiperIndex=current;
		},
		downCallback:function(mescroll){
			pageNum=1;
			console.log(mescroll);
			uni.request({
				header:{
					'sessionId': 'fa2e5c5698b0480eb68ec05fa9a35a3d' //自定义请求头信息
				},
				url: 'http://192.168.1.200:40881/bulletin/queryBulletinTitlesByPage?length=10&pageNum='+pageNum,
				success:function(res) {
					// 成功隐藏下拉加载状态
					console.log(res)
					const data =res.data.data[0].results; 
					
					const data_list = data.map((news) => {
						return{
							id:news.id,
							isread:news.isread,
							title :news.title,
							starttime:friendlyDate(new Date(news.starttime).getTime()),
						};	
					});
					_self.newsList=data_list;
					mescroll.endSuccess();
					
				},
				fail: () => {
					// 失败隐藏下拉加载状态
					mescroll.endErr()
				},
				
			});
			mescroll.resetUpScroll();//重置为第一页
		},
		upCallback :function(mescroll) {
			// 此时mescroll会携带page的参数:
			
			const pageNum = mescroll.num; // 页码, 默认从1开始
			const pageSize = mescroll.size; // 页长, 默认每页10条
			uni.request({
				header:{
					'sessionId': 'fa2e5c5698b0480eb68ec05fa9a35a3d' //自定义请求头信息
				},
				url: 'http://192.168.1.200:40881/bulletin/queryBulletinTitlesByPage?pageNum='+pageNum+'&length='+pageSize,
				success: function(res) {
				console.log()
				// 接口返回的当前页数据列表 (数组)
				const data =res.data.data[0].results;
				
				const data_list = data.map((news) => {
					return{
						id:news.id,
						isread:news.isread,
						title :news.title,
						starttime:friendlyDate(new Date(news.starttime).getTime()),
					};	
				});
				
				//设置列表数据
				if(mescroll.num == 1) _self.newsList = []; //如果是第一页需手动置空列表
				_self.newsList = _self.newsList.concat(data_list); //追加新数据
				// 成功隐藏下拉加载状态
				//方法一(推荐): 判断是否有下一页
				//mescroll.endByPage(curPageData.length, hasNext); 
				//mescroll.endSuccess(data_list.length,newsLength);
					console.log(newsLength)
					mescroll.endBySize(data_list.length, newsLength);

								
			},
				fail: function(){
					// 失败隐藏下拉加载状态
					mescroll.endErr()
					console.log(4454477)
				}
			})
				
		}
		
	},
	
	onReady:function(){
		uni.createSelectorQuery().select("#demo01").fields({
			size:true,
			id:true
		},function(res){
			console.log(res)
			_self.scrollViewHeight=res.height;
			console.log(_self.scrollViewHeight)
		}).exec();
		
		
	},
	components: {
		sunTab,
		MescrollUni,
		sibList
	}
}

